import React from 'react';
import PropTypes from 'prop-types';
import BlcIconBase from 'components/BlcIcon/BlcIconBase';
import { color } from 'public/styles';

const blcIconCheck = props => (
	<BlcIconBase
		color={props.color}
		display={props.display}
		inverted={props.inverted}
		margin={props.margin}
		size={props.size}
		textAlign={props.textAlign}
	>
		<svg
			alt="✓"
			version="1.1"
			viewBox="0 0 32 32"
			xmlns="http://www.w3.org/2000/svg"
		>
			<polygon id="✓" points="23.1583012 6 26 8.1003861 13.3513514 25.1505792 6 17.1196911 8.59459459 14.7413127 13.0733591 19.6216216"></polygon>

			<style jsx>{`
				fill: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
				display: block;
				height: 100%;
				width: 100%;
			`}</style>
		</svg>
	</BlcIconBase>
);

blcIconCheck.propTypes = {
	color: PropTypes.string,
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest', 'inherit'])
}

blcIconCheck.defaultProps = {
	direction: 'right',
	size: null
}

export default blcIconCheck;
