import React from 'react';
import PropTypes from 'prop-types';
import BlcIconBase from 'components/BlcIcon/BlcIconBase';
import { color } from 'public/styles';

const direction = (dir) => {
	switch (dir) {
	case 'top':
		return '270'
	case 'right':
		return '0'
	case 'bottom':
		return '90'
	case 'left':
		return '180'
	default:
		return '0'
	}
}

const blcIconCaret = props => (
	<BlcIconBase
		color={props.color}
		display={props.display}
		inverted={props.inverted}
		margin={props.margin}
		size={props.size}
		textAlign={props.textAlign}
	>
		<svg
			alt="→"
			version="1.1"
			viewBox="0 0 32 32"
			xmlns="http://www.w3.org/2000/svg"
		>
			<path d="M14.416,12.88 C13.7226632,12.2399968 13.0346701,11.5786701 12.352,10.896 L10,8.576 L12.576,6 L23.04,16.464 L12.576,26.944 L10,24.368 L12.352,22.032 C13.0346701,21.3599966 13.7226632,20.6986699 14.416,20.048 C14.8711134,19.6213312 16.0889044,18.4266645 18.069373,16.464 C16.0889044,14.5084466 14.8711134,13.3137799 14.416,12.88 Z"></path>

			<style jsx>{`
				fill: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
				display: block;
				height: 100%;
				width: 100%;

				path {
					transform: rotate(${direction(props.direction)}deg);
					transform-origin: 50%;
				}
			`}</style>
		</svg>
	</BlcIconBase>
);

blcIconCaret.propTypes = {
	color: PropTypes.string,
	direction: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest', 'inherit'])

}

blcIconCaret.defaultProps = {
	direction: 'right',
	size: null
}

export default blcIconCaret;
