import React from 'react';
import PropTypes from 'prop-types';
import BlcIconBase from 'components/BlcIcon/BlcIconBase';
import { color } from 'public/styles';

const direction = (dir) => {
	switch (dir) {
	case 'top':
		return '270'
	case 'right':
		return '0'
	case 'bottom':
		return '90'
	case 'left':
		return '180'
	default:
		return '0'
	}
}

const blcIconArrow = props => (
	<BlcIconBase
		color={props.color}
		display={props.display}
		inverted={props.inverted}
		margin={props.margin}
		size={props.size}
		textAlign={props.textAlign}
	>
		<svg
			alt="→"
			version="1.1"
			viewBox="0 0 32 32"
			xmlns="http://www.w3.org/2000/svg"
		>
			<path d="M3,14.64 L18.52,14.64 C19.2026701,14.64 19.8959965,14.6453333 20.6,14.656 C21.3040035,14.6773334 22.1253286,14.7199997 23.064,14.784 C22.3066629,14.1653302 21.5866701,13.5306699 20.904,12.88 C20.2106632,12.2399968 19.5226701,11.5786701 18.84,10.896 L16.488,8.576 L19.064,6 L29.528,16.464 L19.064,26.944 L16.488,24.368 L18.84,22.032 C19.5226701,21.3599966 20.2106632,20.6986699 20.904,20.048 C21.5866701,19.4079968 22.3066629,18.7786698 23.064,18.16 C22.1253286,18.2240003 21.3040035,18.2613333 20.6,18.272 C19.8959965,18.2826667 19.2026701,18.288 18.52,18.288 L3,18.288 L3,14.64 Z"></path>

			<style jsx>{`
				fill: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
				display: block;
				height: 100%;
				width: 100%;

				path {
					transform: rotate(${direction(props.direction)}deg);
					transform-origin: 50%;
				}
			`}</style>
		</svg>
	</BlcIconBase>
);

blcIconArrow.propTypes = {
	color: PropTypes.string,
	direction: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest', 'inherit'])

}

blcIconArrow.defaultProps = {
	direction: 'right',
	size: null
}

export default blcIconArrow;
